import React from "react"; //  
import {Field, reduxForm} from "redux-form";// reduxForm    
import classes from './FeedBack.module.css'
import {Input, Textarea} from "../common/Validation/customFields";
import {email, Required} from "../common/Validation/validationField";

const FeedBackForm2 = ({handleSubmit}) => { //  
    return (
        <form onSubmit={handleSubmit}/*      reduxForm - handleSubmit*/>
            <fieldset>
                <legend>
                    <div className={classes.legendStyle}> </div>
                </legend>
                <div className={classes.fieldSetStyle}>
                    <div>
                        <Field
                            name="name"//          
                            component={Input}//  - 
                            placeholder="  ( )" //     
                            validate={[]}
                        />
                    </div>
                    <div>
                        <Field
                            name="email"//          
                            component={Input}//  - 
                            placeholder=" Email ( )" //     
                            validate={[email]}
                        />
                    </div>
                    <div>
                        <Field
                            name="message"//          
                            component={Textarea}//  - 
                            placeholder=" " //     
                            validate={[Required]}
                        />
                    </div>
                    <div>
                        <button type="submit"> </button>
                        {/**/}
                    </div>
                </div>
            </fieldset>
        </form>
    )
}

//   ,    "FeedBackForm2"
let FeedBackReduxForm = reduxForm({form: 'FeedBackForm2'})(FeedBackForm2)

let FeedBackForm = ({sendFeedBack}) => {

    let onSubmit = (formData) => { //          formData
        let data = {name: formData.name, email: formData.email, message: formData.message}
        sendFeedBack(data)//       feedBack
    }
    return (
        <div className={classes.FeedBackCommon}/**/ >
            <div className={classes.FeedBackForm2}>
                <div><FeedBackReduxForm
                    onSubmit={onSubmit}
                />{/*        */}
                </div>
            </div>
        </div>
    )
}

export default FeedBackForm
