import React from "react";
import classes from "./FeedBack.module.css";
import FeedBackForm from "./FeedBackForm";

const FeedBack = ({sendFeedBack, feedBackStatus, setFeedBackStatus}) => {
    let SendMessageBoard = (props) => {
        return <div className={classes.sendMessageBoard}>{/*    */}
            {props.children}
        </div>
    }

    if (feedBackStatus) { //   feedback  BLL  
        return (
            <div>
                <SendMessageBoard> {/*    */}
                    <div>!</div>
                    <div>{feedBackStatus}</div>
                    {/* */}
                    <button className={classes.buttonSuccess} onClick={() => {
                        setFeedBackStatus("")
                    }}> 
                    </button>
                    {/*    */}
                </SendMessageBoard>
            </div>
        );
    }

    return (
        <div className={classes.feedBackGreed}> {/*      */}
            <div>
                <FeedBackForm
                    sendFeedBack={sendFeedBack} // redux   
                />
            </div>
        </div>
    );
};

export default FeedBack;
